import './App.css';
import React, {useState} from 'react';

const App = () =>{
  <p>p</p>
  const [count, setCount] = useState(0);
  const [add2, setAdd2] = useState(false);
  const handleClick = () =>{
    if(add2){
      setCount(count +2)
    }
    else{
      setCount(count +1)
    }
  }
  let message;
    if (count > 10){
      message = <h2>This is over ten</h2>;
    }
    else if(count > 4){
     message = <h2>Getting Closer</h2>;
    }
    else {
      message =<h2>Nowhere Near</h2>;
    }
      return(
        
<div className="App-header">
  <div className="holder">
       <h1>Your Current Count is:</h1>
       <h1>{count}</h1>
       {message}
       <div className="buttonHolder">
       <button className="btn" onClick={handleClick}>Add</button>
       <button className="btn2" onClick={() => setAdd2(!add2)}>Toggle x2</button>
       {console.log(add2)}
       </div>
       {add2 && <p>You are currently adding 2 at a time.</p>}
       </div>
       <div className="floatDown">
       <marquee> <p>Designed and devloped by Michael P Cleworth for Code Nation's Master Course (25)</p></marquee> 
    </div></div>
      )}    

  export default App
